const IS_BROWSER = process.env.JUPITER_TARGET === 'browser';
const MOUNT_ID = 'root';
const ROUTER_BASE_NAME= '/index';
const getRouterBaseName = null


import { createApp, bootstrap } from  '@jupiter/plugin-runtime';
import { router} from "@jupiter/plugin-runtime/plugins";

import React from 'react';
import { render, unmountComponentAtNode } from 'react-dom';
import App from '@_jupiter_src/App';
import { ThemeProvider } from '@jupiter/plugin-runtime/styled';

let AppWrapper = null;

const renderApp = () => {
  
  AppWrapper = createApp({
		plugins: [
		router({ basename: ROUTER_BASE_NAME, getRouterBaseName, ...{...undefined,  ...App?.config?.features?.router}, }),
	],
})(App);
    
  bootstrap(AppWrapper, MOUNT_ID)
  
};

renderApp();



export default AppWrapper;


